.card-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 10pt;
}
@media screen and (min-width: 768px) {
.card-container {
      width: 768px;
      display: grid;
      grid-template-areas: "head head" "link skill" "about skill" "experience skill";
      grid-template-columns: 1fr 20rem;
}
.card-container #head {
        grid-area: head;
}
.card-container #link {
        grid-area: link;
}
.card-container #about {
        grid-area: about;
}
.card-container #skill {
        grid-area: skill;
}
.card-container #experience {
        grid-area: experience;
}
}
.card-container .card {
    border-radius: 10pt;
    background: #ffffff;
    -webkit-box-shadow: 1pt 1pt 5pt rgba(66, 125, 143, 0.5);
            box-shadow: 1pt 1pt 5pt rgba(66, 125, 143, 0.5);
    padding: inherit;
    margin: 10pt;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.card-container .card p {
      margin-top: 5pt;
      font-size: 11pt;
      color: #404040;
}
.card-container .card p.tag {
        font-size: 9pt;
        display: inline-block;
        color: #ffffff;
        border-radius: 5pt;
        padding: 0 5pt;
        margin-top: 10pt;
        background: linear-gradient(135deg, #5998EE, #1562cc);
}
.card-container .card.card-head {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-flow: wrap;
          flex-flow: wrap;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      position: relative;
      margin-top: 30pt;
}
.card-container .card.card-head .image {
        background-position: center;
        background-size: cover;
        height: 80pt;
        width: 80pt;
        border-radius: 50%;
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-shadow: 1pt 1pt 5pt rgba(66, 125, 143, 0.5);
                box-shadow: 1pt 1pt 5pt rgba(66, 125, 143, 0.5);
        position: absolute;
        left: 10pt;
        top: -30pt;
}
.card-container .card.card-head .content {
        width: calc(100% - 90pt);
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        margin-left: auto;
}
.card-container .card.card-head .content h1 {
          line-height: 1 !important;
}
@media screen and (max-width: 320px) {
.card-container .card.card-head .content h1 {
              font-size: 22pt;
}
}
.card-container .card.card-head .content p {
